class ImageGallery {
    constructor(selector) {
        this.images = document.querySelectorAll(selector);
        this.modal = null;
        this.init();
    }

    init() {
        this.createModal();
        this.images.forEach(img => {
            img.addEventListener('click', () => this.openModal(img.src));
        });
    }

    createModal() {
        this.modal = document.createElement('div');
        this.modal.style.cssText = `
            display: none;
            position: fixed;
            z-index: 1000;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: rgba(0,0,0,0.8);
            text-align: center;
        `;

        const modalImage = document.createElement('img');
        modalImage.style.cssText = `
            max-width: 90%;
            max-height: 90%;
            margin: auto;
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
        `;

        this.modal.appendChild(modalImage);
        this.modal.addEventListener('click', () => this.closeModal());
        document.body.appendChild(this.modal);
    }

    openModal(src) {
        const modalImage = this.modal.querySelector('img');
        modalImage.src = src;
        this.modal.style.display = 'block';
    }

    closeModal() {
        this.modal.style.display = 'none';
    }
}

class Quest {
    constructor() {
        this.currentScreen = 1;
        this.screens = document.querySelectorAll(".quest-screen");
        this.resultContainer = document.querySelector(".quest-result");
        this.resultList = document.getElementById("result-list");

        this.init();
    }

    init() {
        document.querySelectorAll(".quiz-next").forEach(button => {
            button.addEventListener("click", () => this.nextScreen());
        });

        document.querySelectorAll(".quiz-prev").forEach(button => {
            button.addEventListener("click", () => this.prevScreen());
        });

        document.querySelector(".finish").addEventListener("click", () => this.showResult());
        // document.querySelector(".restart").addEventListener("click", () => this.restart());
    }

    showScreen(index) {
        this.screens.forEach(screen => screen.classList.remove("active"));
        document.querySelector(`.quest-screen[data-screen="${index}"]`).classList.add("active");
    }

    nextScreen() {
        if (this.currentScreen < this.screens.length) {
            this.currentScreen++;
            this.showScreen(this.currentScreen);
        }
    }

    prevScreen() {
        if (this.currentScreen > 1) {
            this.currentScreen--;
            this.showScreen(this.currentScreen);
        }
    }

    showResult() {
        // const answers = [];
        // document.querySelectorAll("input[type='radio']:checked").forEach(input => {
        //     answers.push(`${input.name}: ${input.value}`);
        // });

        // this.resultList.innerHTML = answers.map(answer => `<li>${answer}</li>`).join("");

        this.screens.forEach(screen => screen.classList.remove("active"));
        this.resultContainer.classList.add("active");
    }

    // restart() {
    //     document.querySelectorAll("input[type='radio']").forEach(input => input.checked = false);
    //     this.resultContainer.classList.remove("active");
    //     this.currentScreen = 1;
    //     this.showScreen(this.currentScreen);
    // }
}


